import React, { Suspense, lazy } from "react";
import { Routes, Route } from "react-router-dom";
import Loading from "./components/loading.js";
import { Link as RouterLink } from "react-router-dom";
import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import { Avatar, Grid, Link, Typography } from "@mui/material";
import PersonIcon from '@mui/icons-material/Person';
import AccountTreeIcon from '@mui/icons-material/AccountTree';
import InfoIcon from '@mui/icons-material/Info';
import ConnectWithoutContactIcon from '@mui/icons-material/ConnectWithoutContact';

const Homepage = lazy(() => import("./components/homepage/homepage.js"));
const MyProjects = lazy(() => import("./components/myProjects/myProjects.js"));
const About = lazy(() => import("./components/about/about.js"));
const Contact = lazy(() => import("./components/contact/contact.js"));

const links = [
    { icon: <AccountTreeIcon />, to: 'myProjects', label: 'my projects' },
    { icon: <InfoIcon />, to: 'about', label: 'about me' },
    { icon: <ConnectWithoutContactIcon />, to: 'contact', label: 'contact me' },
];

const App = () => {
    return (
        <ThemeProvider theme={createTheme({ palette: { mode: 'dark' } })}>
            <CssBaseline />
            <Grid sx={{ minWidth: 650, width: "95%", margin: "0 auto", display: 'flex', alignItems: 'center' }} container spacing={2}>
                <Grid item xs={1}>
                    <Avatar sx={{ margin: "0 auto" }}>
                        <PersonIcon />
                    </Avatar>
                </Grid>
                <Grid item xs={11}>
                    <Typography sx={{ display: "inline", textAlign: "left", backgroundImage: "conic-gradient(#553c9a, #ee4b2b, #00c2cb)", color: "transparent", backgroundClip: "text", WebkitBackgroundClip: "text", fontFamily:"Poppins, sans-serif" }} variant="h3">
                        Pak Ling YEUNG, Jason
                    </Typography>
                </Grid>
                {links.map((link, index) => (
                    <Grid item xs={2} sx={{ margin: 1, display: 'flex', alignItems: 'center' }}>
                        <Link
                            key={index}
                            color="inherit"
                            underline="hover"
                            component={RouterLink}
                            to={link.to}
                            sx={{ flex: 1, padding: 1, bgcolor: "darkgray", borderRadius: 2, textAlign: "center" }}
                        >
                            {links.icon}
                            <Typography variant="button" fontWeight="bold">{link.label}</Typography>
                        </Link>
                    </Grid>))}
                <Grid item xs={12}>
                    <Routes>
                        <Route path="/" element={<Suspense fallback={<Loading />}><Homepage /></Suspense>}></Route>
                        <Route path="/myProjects" element={<Suspense fallback={<Loading />}><MyProjects /></Suspense>}></Route>
                        <Route path="/about" element={<Suspense fallback={<Loading />}><About /></Suspense>}></Route>
                        <Route path="/contact" element={<Suspense fallback={<Loading />}><Contact /></Suspense>}></Route>
                    </Routes>
                </Grid>
            </Grid>
        </ThemeProvider>
    );
};

export default App;